.myPosts {
    width: 100%;
    display: flex;
    flex-direction: column;
    max-width: 670px;
}

.myPostsForm {
    width: 100%;
    box-shadow: 2px 2px 9px 0 rgba(126, 131, 135, 0.15);
    background-color: #ffffff;
    border-bottom: 1px solid #ededed;
    border-radius: 10px;
}

.myPostsForm textarea {
    padding: 30px;
    width: 100%;
    height: 150px;
    font-family: inherit;
    font-size: 16px;
    border-radius: 10px 10px 0 0;
    border: 1px solid #ededed;
    outline: none;
}

.myPostsForm button {
    display: block;
    width: 120px;
    margin: -3px 0 0 auto;
    padding: 7px 7px;
    font-size: 14px;
}

h2 {
    font-weight: 700;
}

.postList {
    width: 100%;
    height: 100%;
    margin-top: 30px;
    padding: 30px;
    box-shadow: 2px 2px 9px 0 rgba(126, 131, 135, 0.15);
    background-color: #ffffff;
    border-radius: 10px;
}


